import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function Introduction () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>magic-runes-introduction</SubjectIdentifier>
      <SubjectKeyword>Maîtrise</SubjectKeyword>
      <SubjectTitle>Introduction</SubjectTitle>
      <SubjectSummary>
      </SubjectSummary>
      <SubjectContent>
        <p>
          L'astral est une langue parlée qui peut être retranscrite par l'usage
          de symboles tracés au pinceau appelés runes. Les runes se lisent de
          haut en bas et de gauche à droite. Il est donc possible de
          retranscrire une évocation en écrivant des pages finies faites de
          multiples colones et de lignes, sous forme de volumen constitué d'une
          seule ligne ou sous la forme de rotulus constitué d'une seule colone.
        </p>
      </SubjectContent>
    </Subject>
  )
}
